import { Component } from '@angular/core';
import { NavController, SegmentButton, AlertController, IonicPage, NavParams, PopoverController, ModalController } from 'ionic-angular';
import { Camera,CameraOptions } from '@ionic-native/camera';
import { PopoverPage } from '../popover/popover';
import { CreateTicketPage } from '../create-ticket/create-ticket';

/**
 * Generated class for the TicketPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ticket',
  templateUrl: 'ticket.html',
})
export class TicketPage {
  whichSegment: any;
  segment_active: string;
  isSearching: boolean = false;

  constructor(public modalCtrl:ModalController,public popoverCtrl:PopoverController,private camera:Camera,public navCtrl: NavController, public navParams: NavParams, public alertCtrl: AlertController) {
    this.whichSegment = "first";
  }

  openSearchBox() {
    this.isSearching = true;
  }

  closeSearchBox() {
    this.isSearching = false;
  }

  onCancel(e) {
    console.log('cancel');
    this.isSearching = false;
  }

  onInput(e) {
    console.log(e);
  }

  openPopup(myEvent) {
    let popover = this.popoverCtrl.create(PopoverPage);
    popover.present({
      ev: myEvent
    });
    // popover.onDidDismiss((data) => {
    //   this.events.subscribe('dismiss', angka => {
    //     if (angka.data != undefined) {
    //       this.stillLoading = true;
    //       this.jadwal = {};
    //       this.waktuSalat = null;
    //       this.selisih = { value: null };
    //       this.getTime(angka.data.city);
    //       this.cityDesc = angka.data.desc;
    //       this.events.unsubscribe('dismiss')
    //     }
    //   })

    //   if (data == 'hideKutipan') this.hideIt = true;
    // });
  }

  toCreateTicket() {
    let profileModal = this.modalCtrl.create(CreateTicketPage);
    profileModal.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TicketPage');
  }

  onSegmentChange(segmentButton: SegmentButton) {
    this.segment_active = segmentButton.value;
    console.log(this.segment_active);
    if (this.segment_active == 'first') {
      this.whichSegment = "first";
      console.log('first')
    } else {
      console.log('second')
      this.whichSegment = "second";
    }
  }

  status(status) {
    console.log(status);
    const alerts = this.alertCtrl.create({
      title: 'Status',
      message: status,
      buttons: ['OK'],
      enableBackdropDismiss: false
    })
    alerts.present();
  }

  toSecondSegment() {
    this.whichSegment = "second";
  }

 
}



