import { Component } from '@angular/core';
import { NavController, SegmentButton, AlertController, ModalController, PopoverController } from 'ionic-angular';
import { StatusBar } from '../../../node_modules/@ionic-native/status-bar';
import { CreateMessagePage } from '../create-message/create-message';
import { MessageDetailPage } from '../message-detail/message-detail';
import { PopoverPage } from '../popover/popover';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
  whichSegment    : any;
  segment_active  : string;
  isCreateMessage : boolean = false;
  isSearching     : boolean = false;
  myInput         : string;

  constructor(public navCtrl: NavController, public statusBar: StatusBar, public modalCtrl: ModalController, public popoverCtrl: PopoverController, public alertCtrl: AlertController) {
    this.whichSegment = "first";
  }

  onSegmentChange(segmentButton: SegmentButton) {
    this.segment_active = segmentButton.value;
    console.log(this.segment_active);
    if (this.segment_active == 'first') {
      this.whichSegment = "first";
      this.isCreateMessage = false;
      console.log('first')
    } else {
      console.log('second')
      this.whichSegment = "second";
      this.isCreateMessage = true;
    }
  }

  openSearchBox() {
    this.isSearching = true;
  }

  closeSearchBox() {
    this.isSearching = false;
  }

  onCancel(e) {
    console.log('cancel');
    this.isSearching = false;
  }

  onInput(e) {
    console.log(e);
  }

  openPopup(myEvent) {
    let popover = this.popoverCtrl.create(PopoverPage);
    popover.present({
      ev: myEvent
    });
    // popover.onDidDismiss((data) => {
    //   this.events.subscribe('dismiss', angka => {
    //     if (angka.data != undefined) {
    //       this.stillLoading = true;
    //       this.jadwal = {};
    //       this.waktuSalat = null;
    //       this.selisih = { value: null };
    //       this.getTime(angka.data.city);
    //       this.cityDesc = angka.data.desc;
    //       this.events.unsubscribe('dismiss')
    //     }
    //   })

    //   if (data == 'hideKutipan') this.hideIt = true;
    // });
  }

  toCreateMessage() {
    this.navCtrl.push(CreateMessagePage);
  }

  toMessageDetail() {
    let modal = this.modalCtrl.create(MessageDetailPage, { data: null }, { enableBackdropDismiss: true, cssClass: 'select-modal' });
    modal.present();
  }

  send(status) {
    console.log(status);
    const alerts = this.alertCtrl.create({
      title: 'Status',
      message: status,
      buttons: ['OK'],
      enableBackdropDismiss: false
    })
    alerts.present();
  }

  toSecondSegment() {
    this.whichSegment = "second";
  }

}



