import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, AlertController } from 'ionic-angular';
import { Camera,CameraOptions } from '../../../node_modules/@ionic-native/camera';

/**
 * Generated class for the CreateTicketPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-create-ticket',
  templateUrl: 'create-ticket.html',
})
export class CreateTicketPage {
  isHidden  : boolean = false;
  imageUri : string = ""; 

  constructor(public alertCtrl :AlertController,public camera:Camera,public navCtrl: NavController, public viewCtrl: ViewController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreateTicketPage');
  }

  closeModal() {
    this.viewCtrl.dismiss();
  }


  isUseBarcode() {
    this.isHidden = !this.isHidden;
  }

  openCamera(){
    console.log('camera open')
    // console.log(this.imageUri)
    // // this.imageUri = 'kkl'
    // const options: CameraOptions = {
    //   quality: 100,
    //   destinationType: this.camera.DestinationType.FILE_URI,
    //   encodingType: this.camera.EncodingType.JPEG,
    //   mediaType: this.camera.MediaType.PICTURE
    // }
    
    // this.camera.getPicture(options).then((imageData) => {
    //  // imageData is either a base64 encoded string or a file URI
    //  // If it's base64 (DATA_URL):
    //  this.imageUri = 'data:image/jpeg;base64,' + imageData;
    // //  this.imageUri = base64Image;
    //  console.log(this.imageUri)
    // }, (err) => {
    //  // Handle error
    // });
    let  options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE
    }


    this.camera.getPicture(options).then((imageData) => {
      this.imageUri = 'data:image/jpeg;base64,' + imageData;
      // this.imageUri = imageData;
      console.log(this.imageUri)
     
      }, (err) => {
        this.displayErrorAlert(err);
      });
  }

  displayErrorAlert(err){
    console.log(err);
    let alert = this.alertCtrl.create({
       title: 'Error',
       subTitle: 'Error while trying to capture picture',
       buttons: ['OK']
     });
     alert.present();
  }

}



